import { FastError } from "../error";

/**
 * 
 */
export const colorUtil = {
	/**
	 * hexrgb
	 * @param str 
	 * @returns 
	 */
	hexToRgb(str: any): any {
		let hex: any = "";
		const reg = /^#?[0-9A-F]{6}$/i;
		if (!reg.test(str)) throw new FastError("hex");
		str = str.replace("#", "");
		hex = str.match(/../g);
		for (let i = 0; i < 3; i++) hex[i] = parseInt(hex[i], 16);
		return hex;
	},
	/**
	 * rgbHex
	 * @param r 
	 * @param g 
	 * @param b 
	 * @returns 
	 */
	rgbToHex(r: any, g: any, b: any): string {
		const reg = /^\d{1,3}$/;
		if (!reg.test(r) || !reg.test(g) || !reg.test(b)) throw new FastError("rgb");
		const hex = [r.toString(16), g.toString(16), b.toString(16)];
		for (let i = 0; i < 3; i++) if (hex[i].length === 1) hex[i] = `0${hex[i]}`;
		return `#${hex.join("")}`;
	},
	/**
	 * 
	 * @param color 
	 * @param level 0-1
	 * @returns 
	 */
	getDarkColor(color: string, level: number): string {
		const reg = /^#?[0-9A-F]{6}$/i;
		if (!reg.test(color)) throw new FastError("hex");
		const rgb = this.hexToRgb(color);
		for (let i = 0; i < 3; i++) rgb[i] = Math.round(20.5 * level + rgb[i] * (1 - level));
		return this.rgbToHex(rgb[0], rgb[1], rgb[2]);
	},
	/**
	 * 
	 * @param color 
	 * @param level 0-1
	 * @returns 
	 */
	getLightColor(color: string, level: number): string {
		const reg = /^#?[0-9A-F]{6}$/i;
		if (!reg.test(color)) throw new FastError("hex");
		const rgb = this.hexToRgb(color);
		for (let i = 0; i < 3; i++) rgb[i] = Math.round(255 * level + rgb[i] * (1 - level));
		return this.rgbToHex(rgb[0], rgb[1], rgb[2]);
	},
};
