let _debounceTimeout: ReturnType<typeof setTimeout> | null = null;
let _throttleRunning = false;

/**
 * 
 */
export const clickUtil = {
	/**
	 * 
	 * @param fn - 
	 * @param delay - 
	 * @returns 
	 */
	debounce(fn: () => void, delay = 500): void {
		// 
		if (_debounceTimeout) {
			clearTimeout(_debounceTimeout);
		}
		// 
		_debounceTimeout = setTimeout(() => {
			// 
			fn();
		}, delay);
	},
	/**
	 * 
	 * @param fn - 
	 * @param delay - 
	 * @returns 
	 */
	async debounceAsync(fn: () => Promise<void>, delay = 500): Promise<void> {
		return new Promise((resolve, reject) => {
			// 
			if (_debounceTimeout) {
				clearTimeout(_debounceTimeout);
			}
			// 
			_debounceTimeout = setTimeout(async () => {
				try {
					//  fn
					await fn();
					resolve();
				} catch (error) {
					reject(error);
				}
			}, delay);
		});
	},
	/**
	 * 
	 * @param fn - 
	 * @param delay - 
	 * @returns 
	 */
	throttle(fn: () => void, delay = 500): void {
		// 
		if (_throttleRunning) {
			return;
		}
		// 
		_throttleRunning = true;
		// 
		fn();
		// 
		setTimeout(() => {
			_throttleRunning = false;
		}, delay);
	},
	/**
	 * 
	 * @param fn - 
	 * @param delay - 
	 * @returns 
	 */
	async throttleAsync(fn: () => Promise<void>, delay = 500): Promise<void> {
		return new Promise((resolve, reject) => {
			// 
			if (_throttleRunning) {
				return;
			}
			// 
			_throttleRunning = true;
			// 
			fn()
				.then(() => {
					resolve();
				})
				.catch((error) => {
					reject(error);
				})
				.finally(() => {
					// 
					setTimeout(() => {
						_throttleRunning = false;
					}, delay);
				});
		});
	},
};
